import { Plugin } from '@ali/4ever-cangjie';
import { ColumnsPlugin } from '@ali/4ever-bamboo';

import setColumnsItemData from '../commands/setColumnsItemData';

const { hooks, createQueries } = ColumnsPlugin;

function createColumnsPlugin(allConfigs): Plugin {
  const {
    createRenderNode,
    createOnAction,
    createOnCangjieSelect,
    createOnKeyDown,
    createPlaceholder,
    createOnCut,
    beforeCopySelectedNodes,
  } = hooks;
  const configs = allConfigs.columns;
  return {
    commands: { setColumnsItemData },
    queries: createQueries(configs),
    renderNode: createRenderNode(configs),
    onAction: createOnAction(configs),
    onCangjieSelect: createOnCangjieSelect(configs),
    onKeyDown: createOnKeyDown(),
    onCut: createOnCut(),
    // @ts-ignore
    placeholder: createPlaceholder(configs),
    beforeCopySelectedNodes,
  };
}

export default createColumnsPlugin;
